@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;700&family=Reenie+Beanie&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
    display: flex;
    flex-direction: row;
    width: 100vw;
    top: 0;
    position: -webkit-sticky;
    position: sticky;
    background-color: white;
    padding-bottom: 10px;
}
.nav-links {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    flex: 0.6 1;
    justify-content: space-between;
    text-align: center;
    margin-top: 32px;
    margin-left: 28vw;
}

.nav-links a{  
    text-decoration: none; 
    color: #1C2E43;   
}

.nav-links a:hover{  
    text-decoration: underline;   
}
* {
    font-family: 'Quicksand';
}

h1 {
    font-family: 'Reenie Beanie';
    font-size: 64px;
    margin-bottom: 0;
}

p {
    line-height: 32px;
}

.block {
    display: flex;
    flex-direction: row;
    margin-right: 80px;
    margin-left: 80px;
    margin-bottom: 250px;
    margin-top: 7.5vw;
}

a {
    text-decoration: none;
}

.text-container {
    width: 35vw;
    margin-left: 105px;
    margin-right: 105px;
    margin-top: 32px;
}
.image-container {
    margin-left: 100px;
    margin-right: 100px;
}


.workBkgd {
    background-color: #FDFBF2;
    width: 60vw;
    margin-left: 200px;
    border-radius: 8px;
    display: flex;
    flex-direction: row;
    margin-bottom: 34px;
}

.compBkgd {
    width: 18vw;
    border-radius: 10px;
    margin-top: 2.5vw;
    margin-left: 36px;
    margin-right: 36px;
    margin-bottom: 2.5vw;
    display: flex;
    flex-direction: column;
}

.companyName {
    font-size: 24px;
    letter-spacing: 5px;
    font-weight: 300;
    margin-left: 1.5vw;
    margin-bottom: 6vw;
}

.workInfo {
    display: flex;
    flex-direction: column;
    margin-left: 1.5vw;
    font-size: 14px; 
    line-height: 0px;
}

.workCard {
    display: flex;
    flex-direction: row;
    margin-top: 1vw;
    font-size: 20px;
}

.workDesc {
    display: flex;
    flex-direction: column;
    line-height: 24px;
    width: 35vw;
}

.workDescItems {
    margin-bottom: 8px;
}
.workBody {
    display: flex;
    flex-direction: column;
    margin: -100px 0px 200px 100px;
}
.projDesc {
    margin-left: 30px;
    margin-right: 40px;
    line-height: 24px;
}

.projName {
    font-size: 22px;
    margin-left: 30px; 
    line-height: 25px;
    font-weight: 400;
}

a:hover {
    text-decoration: underline;
}

.card {
    padding-top: 20px;
    border-radius: 7px;
}






.projBody {
    display: flex;
    flex-direction: column;
    margin: -100px 0px 200px 200px;
}

.grid-container {
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-gap: 1em 1em;
    width: 80%;
}

.grid-item {
    font-size: 16px;
}

.grid-item a {
    color: #1C2E43;
}

.item1 {
    grid-row: 1;
    grid-column: 1 / span 2;
    color: #1C2E43;
}

.item2 {
    grid-row: 1;
    grid-column: 3 / span 2;
    color: #F2F2F2;
}

.item3 {
    grid-row: 2;
    grid-column: 1 / span 2;
    color: #1C2E43;
}

.item4 {
    grid-row: 2;
    grid-column: 3 / span 2;
    color: #1C2E43;
}
.item5 {
    grid-row: 3;
    grid-column: 1 / span 4;
    color: #1C2E43;
}

.item2 a {
    color: #F2F2F2;
}
.about-col {
    display: flex;
    flex-direction: column;
    margin-right: -100px;
}

.about-row {
    display: flex;
    flex-direction: row;
    margin-left: 80px;
    margin-top: 32px;
}

.copy-text {
    font-size: 12px;
    text-align: center;
    line-height: 12px;
}
